@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+KR:wght@300&display=swap');

.service-card {

  width: 20%;
  height: 350px;
  color: white;
  background-color: #212754;
  border-radius: 25px;
  margin-left: 6%;
  transition: color .6s, background-color .6s ease;
  

}

.service-card:hover {
  
  background-color: white;
  color: #212754;
  

}

.service-card-inside {

  height: 100%;
  width: 75%;
  margin-left: 12.5%;
}

.service-card-inside h1 {

  font-size: 20px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  padding-top: 10%;

}

.service-card h4 {

  padding-top: 5%;
  font-family: 'IBM Plex Sans KR', sans-serif;
  font-weight: 100;
  font-size: 15px;
  padding-top: 10%;
}